@charset "UTF-8";
nav {
  width: 540px;
  height: 190px;
  text-align: right;
  float: left;
}
nav ul {
  list-style: none;
  margin: 95px 0 0 0;
  padding: 0;
}
nav ul li {
  display: inline;
  min-width: 20%;
  font-size: 0.8em;
  margin-right: 10px;
}
nav ul li a {
  color: #E65C43;
  text-decoration: none;
  letter-spacing: -0.01em;
  /* change this */
}
nav ul li a:hover, nav ul li a:focus {
  color: #30353D; /* was $color-white */
  text-decoration: underline;
}

/* BASIC / BODY */
html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  line-height: 2em;
  background: url("../../images/bg.gif") #fff repeat-x;
  margin: 0;
}

main {
  width: 940px;
  margin: 0 auto;
  padding: 0 10px;
}

/* NAV + LOGO */
figure {
  width: 400px;
  height: 190px;
  background: url("../../images/logo.png") no-repeat 0 50px;
  display: block;
  margin: 0;
  float: left;
}

/* HEADER */
header {
  width: inherit;
  clear: both;
  text-align: center;
  padding-top: 40px;
}
header img {
  max-width: 100%;
  height: auto;
}
header h1 {
  color: #E65C43;
  font-weight: 100;
  font-size: 4em;
  line-height: 0;
}
header h2 {
  color: #fff;
  font-weight: 900;
  font-size: 2.5em;
  line-height: 0;
  margin-bottom: 70px;
}
header h3 {
  color: #666;
  font-weight: 400;
  font-size: 2em;
  line-height: 0;
  margin-bottom: 50px;
}

/* CONTENT */
#headline {
  width: 100%;
  height: 130px;
  background: #E65C43;
  text-align: center;
  margin: 0 auto;
}
#headline h1 {
  color: #fff;
  font-size: 2.5em;
  font-weight: 100;
  padding-top: 50px;
  margin: 0;
}

#content {
  width: 940px;
  margin: 20px auto;
}
#content h3 {
  color: #666;
  font-size: 2em;
  font-weight: 400;
}
#content #products {
  width: 460px;
  float: left;
}
#content #products img {
  float: left;
  margin-right: 20px;
}
#content #products article {
  margin-bottom: 40px;
}
#content #products h4 {
  line-height: 1em;
}
#content #testimonials {
  width: 460px;
  float: right;
}

/* FOOTER + GREY BAR */
.greybar {
  width: 100%;
  height: 90px;
  background: #F5F6F6;
  clear: both;
  padding-top: 30px;
}

footer {
  width: 100%;
  height: 120px;
  background: #30353D;
  text-align: center;
  color: #fff;
  padding-top: 50px;
}
footer .social-media {
  margin: 0 auto;
  width: 165px;
}
footer .social-media div {
  float: left;
  margin-right: 5px;
}
footer .social-media a {
  display: block;
  width: 36px;
  height: 36px;
  text-indent: -9999px;
}
footer .social-media .one {
  background: url("../../images/social-media.png") 0 0;
}
footer .social-media .two {
  background: url("../../images/social-media.png") -38px 0;
}
footer .social-media .three {
  background: url("../../images/social-media.png") -76px 0;
}
footer .social-media .four {
  background: url("../../images/social-media.png") -114px 0;
}

/* MEDIA QUERIES â€” same breakpoints & behavior as original CSS */
@media screen and (max-width: 959px) {
  main {
    width: 768px;
  }
  figure {
    width: inherit;
    height: 140px;
    float: none;
    background-position: center;
  }
  nav {
    width: inherit;
    height: 50px;
    float: none;
    text-align: center;
  }
  nav ul {
    margin-top: 0;
  }
  nav ul li {
    font-size: 1em;
    margin: 0 10px;
  }
  header h1 {
    font-size: 3em;
  }
  header h2 {
    font-size: 1.5em;
  }
  #content {
    width: inherit;
  }
  #content #products,
  #content #testimonials {
    width: 46%;
    margin: 0 2%;
  }
}
@media screen and (max-width: 767px) {
  main {
    width: 460px;
  }
  nav ul li {
    font-size: 0.8em;
    margin: 0 5px;
  }
  header h1 {
    font-size: 2em;
  }
  header h2 {
    font-size: 1em;
  }
  header img {
    margin-bottom: 100px;
  }
  header h3 {
    font-size: 1.5em;
  }
  #headline h1 {
    font-size: 2em;
    padding-top: 50px;
  }
  #content #products,
  #content #testimonials {
    width: 96%;
    margin: 0 2%;
    float: none;
  }
}
@media screen and (max-width: 459px) {
  main {
    width: 300px;
  }
  figure {
    background-size: contain;
    height: 100px;
  }
  nav {
    margin-bottom: 40px !important;
  }
  select {
    width: 95%;
  }
  #headline h1 {
    font-size: 1.5em;
    padding-top: 30px;
  }
  header img {
    margin-bottom: 80px;
  }
  header h1:first-child {
    font-size: 1.4em;
    margin-bottom: 20px;
  }
  header h2 {
    font-size: 1.4em;
  }
  header h3 {
    color: #fff;
    font-size: 1em;
  }
  #content h3 {
    font-size: 1.6em;
  }
  .greybar, article img {
    display: none;
  }
  footer {
    text-align: center;
  }
  footer p, footer img {
    float: none;
    margin: 0;
  }
}